.searchItem {
  border: 1px solid lightgray;
  padding: 10px;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  gap: 20px;
  margin-bottom: 20px;
}
.searchImg {
  width: 200px;
  height: 200px;
  object-fit: cover;
}
.siDesc {
  display: flex;
  flex-direction: column;
  gap: 10px;
  flex: 2;
}
.siTitle {
  font-size: 20px;
  color: #0071c2;
}
siDistance {
  font-size: 12px;
}
.siTaxiOp {
  font-size: 12px;
  background: #008009;
  color: #fff;
  width: max-content;
  padding: 5px;
  border-radius: 5px;
}
.siSubtitle {
  font-size: 12px;
  font-weight: bold;
}
.siFeatures {
  font-size: 12px;
}
.siCancelOp {
  font-size: 12px;
  color: #008009;
  font-weight: bold;
}
.cancelOptSUbtitle {
  color: #008009;
}
.siDetails {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.siRating {
  display: flex;
  justify-content: space-between;
}
.siRating > span {
  font-weight: 500;
}
.siRating > button {
  background-color: #003580;
  color: white;
  padding: 5px;
  font-weight: bold;
  border: none;
  outline: none;
}
.siDetailTexts {
  text-align: right;
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.siPrice {
  font-size: 24px;
}
.siTaxOpt {
  font-size: 12px;
  color: #555;
}
.siCheckButton {
  outline: none;
  font-weight: bold;
  padding: 10px 5px;
  /* border: none; */
  cursor: pointer;
  border-radius: 5px;
  background: #003580;
  color: #fff;
}
