.featured {
  width: 100%;
  max-width: 1024px;
  display: flex;
  justify-content: space-between;
  gap: 20px;
}
.featuredItems {
  position: relative;
  color: white;
  border-radius: 10px;
  overflow: hidden;
  -ms-hyphenate-limit-zone: 250px;
}
.featuredImg {
  width: 100%;
  object-fit: cover;
}
.featuredTitles {
  position: absolute;
  bottom: 20px;
  left: 50px;
}
